import { createSlice } from '@reduxjs/toolkit';

const analogSeriesSlice = createSlice({
    name: 'analogSeries',
    initialState: { data: null },
    reducers: {
        setAnalogSeriesData: (state, action) => {
            state.data = action.payload;
        }
    }
})

export const { setAnalogSeriesData } = analogSeriesSlice.actions;
export default analogSeriesSlice;