import React from "react";
import { motion, useViewportScroll, useTransform } from "framer-motion";
import classes from "./AboutPage.module.css";
import GautOne from "../assets/img/digital/GautOne.jpg";
import GautTwo from "../assets/img/digital/GautTwo.jpg";

const images = [GautOne, GautTwo]; // Your image paths

const ImageSlider = () => {
  const { scrollY } = useViewportScroll();
  const opacity = useTransform(scrollY, [0, 120], [1, 0]);
  const opacityInverted = useTransform(scrollY, [0, 120], [0, 1]);

  return (
    <section>
      <div className={classes.mainAboutDiv}>
        <h1>GAUTHIER TAMPERE</h1>
        <div className={classes.imageContainer}>
          <motion.img
            src={images[0]}
            alt="image-0"
            className={classes.image}
            style={{ opacity: opacityInverted }}
          />
          <motion.img
            src={images[1]}
            alt="image-1"
            className={classes.image}
            style={{ opacity }}
          />
        </div>
        <p>
          GAUTHIER comes from a small town in rural Belgium but has left his
          homestead ever since he bought his first VW. In summer youll find him
          under the sun near the ocean in the south of Europe. In winter youll
          find him amongst the Gods high up in the mountains in Switzerland.
          Youll find a similar duality in his art. Gauthier Switches back and
          forth between analog and digital photography. In the past years he
          developed a keen eye and deep knowledge of his craft. Thus allowing
          him to pick the right camera for the right moment.
        </p>
      </div>
    </section>
  );
};

export default ImageSlider;
