import { configureStore } from "@reduxjs/toolkit";
import analogSeriesSlice from "./analogSeriesSlice";
import digitalSeriesSlice from "./digitalSeriesSlice";


const reduxStore = configureStore({
  reducer: {
    analogSeries: analogSeriesSlice.reducer,
    digitalSeries: digitalSeriesSlice.reducer,
  },
});

export default reduxStore;
