import { useState } from "react";
import classes from "./AnalogItem.module.css";
import Modal from "./Modal";
import { motion } from "framer-motion";

export default function AnalogItem({ series }) {
  const [selectedImage, setSelectedImage] = useState(null);
  const [showModal, setShowModal] = useState(false);

  function handleImageClick(image) {
    setSelectedImage(image);
    setShowModal(true);
  }

  function closeModal() {
    setShowModal(false);
    setSelectedImage(null);
  }

  return (
    <>
      <section className={classes.introHeader}>
        <h4>{series.name} SERIES</h4>
        <div className={classes.imageContainerHeader}>
          <img src={series.coverImage} alt={series.name} />
        </div>
        <p>{series.text}</p>
      </section>

      <section className={classes.gallery}>
        {series.pictures.map((image, index) => (
          <motion.figure
            key={index}
            className={`${classes.galleryItem} ${
              classes[`galleryItem${index}`]
            }`}
            onClick={() => handleImageClick(image)}
            initial={{  scale: .8}}
            whileInView={{  scale: 1}}
            viewport={{ once: true }}
          >
            <img src={image} alt={`picture`} />
          </motion.figure>
        ))}
      </section>
      {/* <section className={classes.gallery}>
            <figure className={`${classes.galleryItem} ${classes.galleryItemOne}`} onClick={() => handleImageClick(series.pictures[0])}>
                <img src={series.pictures[0]} alt={`picture`} />
              </figure> 
              <figure className={`${classes.galleryItem} ${classes.galleryItemTwo}`} onClick={() => handleImageClick(series.pictures[1])}>
                <img src={series.pictures[1]} alt={`picture`} />
              </figure> 
              <figure className={`${classes.galleryItem} ${classes.galleryItemThree}`} onClick={() => handleImageClick(series.pictures[2])}>
                <img src={series.pictures[2]} alt={`picture`} />
              </figure> 
              <figure className={`${classes.galleryItem} ${classes.galleryItemFour}`} onClick={() => handleImageClick(series.pictures[3])}>
                <img src={series.pictures[3]} alt={`picture`} />
              </figure>
              <figure className={`${classes.galleryItem} ${classes.galleryItemFive}`} onClick={() => handleImageClick(series.pictures[4])}>
                <img src={series.pictures[4]} alt={`picture`} />
              </figure>
              <figure className={`${classes.galleryItem} ${classes.galleryItemSix}`} onClick={() => handleImageClick(series.pictures[5])}>
                <img src={series.pictures[5]} alt={`picture`} />
              </figure>
              <figure className={`${classes.galleryItem} ${classes.galleryItemSeven}`} onClick={() => handleImageClick(series.pictures[6])}>
                <img src={series.pictures[6]} alt={`picture`} />
              </figure>
              <figure className={`${classes.galleryItem} ${classes.galleryItemEight}`} onClick={() => handleImageClick(series.pictures[7])}>
                <img src={series.pictures[7]} alt={`picture`} />
              </figure>
        </section> */}
      {showModal && selectedImage && (
        <Modal image={selectedImage} closeModal={closeModal} />
      )}
    </>
  );
}
