import { createSlice } from '@reduxjs/toolkit';

const digitalSeriesSlice = createSlice({
    name: 'digitalSeries',
    initialState: { data: null },
    reducers: {
        setDigitalSeriesData: (state, action) => {
            state.data = action.payload;
        }
    }
})

export const { setDigitalSeriesData } = digitalSeriesSlice.actions;
export default digitalSeriesSlice;